









.slide {
  text-align: center;
  margin: 0 auto;
}

.baner {
  z-index: 0;
  width: 100%;
  height: 450px;
  padding-left: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.baner li {
  list-style: none;
}

.baner li a {
  z-index: 1;
  text-indent: -9999em;
  display: block;
  position: absolute;
}

.baner li span {
  color: #0000;
  width: 100%;
  height: 100%;
  animation: 25s infinite imageAnimation;
  position: absolute;
  top: 0;
  left: 0;
}

.baner li:nth-child(2) span {
  animation-delay: 8s;
}

.baner li:nth-child(3) span {
  animation-delay: 16s;
}

@keyframes imageAnimation {
  0% {
    opacity: 0;
  }

  13% {
    opacity: 1;
  }

  25% {
    opacity: 1;
  }

  37% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

